/*------------------------------------------------------------------------------
      Sizes
------------------------------------------------------------------------------*/
/* Exact pixel values @ 14px em */
.sans-serif {
  font-family: 'Montserrat', 'Helvetica', 'arial' sans-serif !important;
}
.subheading {
  font-family: 'Droid Serif', serif !important;
  font-style: italic;
}
.text-center {
  text-align: center;
}
@font-face {
  font-family: 'Icons';
  src: url("../fonts/icons.eot");
  src: url("../fonts/icons.eot?#iefix") format('embedded-opentype'), url("../fonts/icons.woff2") format('woff'), url("../fonts/icons.woff") format('woff'), url("../fonts/icons.ttf") format('truetype'), url("../fonts/icons.svg#icons") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3rem;
}
h3 {
  font-size: 2rem;
}
h4 {
  font-size: 1.75rem;
}
h5 {
  font-size: 1.2rem;
}
h6 {
  font-size: 1rem;
}
.wrapper {
  margin-top: 40px;
}
.gs-tool-section .column {
  margin: 60px 0;
}
a {
  color: #5829bb;
}
div#lang_sel_click {
  display: inline-block;
}
#top-menu {
  padding-top: 5px;
  background-color: white;
}
#top-menu.ui.menu {
  border: 0px solid rgba(0, 0, 0, 0) !important;
  -webkit-box-shadow: 0 0 0 0 !important;
  box-shadow: 0 0 0 0 !important;
  border-radius: 0 !important;
}
footer.ui.segment {
  margin-bottom: 0;
}
footer.ui.segment .footer-menu ul {
  list-style-type: none;
  padding-left: 0;
}
footer.ui.segment .footer-menu li {
  padding: 5px;
}
footer.ui.segment .footer-menu li:hover a {
  color: #5829bb;
}
footer.ui.segment .footer-menu a {
  color: black;
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
#toolbox-filter {
  margin-left: 7px;
  margin-top: 10px;
}
.isotope:after {
  content: '';
  display: block;
  clear: both;
}
.card .image img {
  background: #fff;
}
.ui.cards > .card > .image:not(.ui) > img {
  border: 0;
}
.ui.cards > .card > .content {
  border-top: 0;
}
.ui.card > :first-child,
.ui.cards > .card > :first-child {
  border-radius: 0!important;
}
.ui.card > .content > a.header:hover,
.ui.cards > .card > .content > a.header:hover {
  color: #5829bb;
}
.ui.card > :last-child,
.ui.cards > .card > :last-child {
  border-radius: .28571429rem!important;
}
.ui-group .ui.basic.button {
  margin-bottom: 5px;
}
#filter-branch {
  padding-top: 10px;
  padding-bottom: 10px;
}
#filter-phase {
  padding-bottom: 10px;
}
.isotope-item {
  position: relative;
  float: left;
  margin: 10px 50px ;
  padding: 10px;
  color: #262524;
  display: inline-block;
}
.isotope-item > * {
  margin: 0;
  padding: 0;
}
#landing-search {
  position: relative;
  width: 100%;
  min-height: auto;
  overflow-y: hidden;
  background: url("../images/shattered.png"), #5829bb;
  /* fallback for old browsers */
  background: url("../images/shattered.png"), -webkit-linear-gradient(to left, #5829bb, #dc2430);
  /* Chrome 10-25, Safari 5.1-6 */
  background: url("../images/map-image.png"), linear-gradient(to left, #5829bb, #dc2430);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
}
.ui.card,
.ui.cards > .card {
  transition: box-shadow 0.1s ease;
}
body {
  margin: 12px !important;
}
