@theme-secondary:  #5829BB;
@theme-tertiary:   #dc2430;

#landing-search {
    position: relative;
    width: 100%;
    min-height: auto;
    overflow-y: hidden;
    background: url("../images/shattered.png"), @theme-secondary; /* fallback for old browsers */
    background: url("../images/shattered.png"), -webkit-linear-gradient(to left, @theme-secondary , @theme-tertiary); /* Chrome 10-25, Safari 5.1-6 */
    background: url("../images/map-image.png"), linear-gradient(to left, @theme-secondary , @theme-tertiary); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
}
